$font-family: "InterTight";
$bg-color: #e5edf7;
$color: #133a66;
$accent-color: #4995EC;
$light-color: #f3f9ff;
$gradient-color-accent: #4995EC;
$gradient-color-light: #83CAE8;
//base
$color-white: #fff;
$max-width: 1880px;
$font-size: clamp(1rem, 0.7143rem + 0.5714vw, 1.25rem);

@import 'vendor/fonts';
@import 'vendor/_normalize';
@import 'vendor/header';

html {
	scroll-behavior: smooth;
}

body {
	font-family: $font-family;
}

body {
	min-inline-size: 320px;
	margin: 0 auto;
	font-family: sans-serif;
	letter-spacing: 0;
	font-size: $font-size;
	font-weight: 400;
	background-color: $bg-color;
	color: $color;
}

@supports (font-variation-settings: normal) {
	body {
		font-family: $font-family, sans-serif;
	}
}

.logo {
	display: flex;
	gap: 10px;
	align-items: center;
	justify-content: center;

	&__picture {
		flex-shrink: 0;

		img {
			max-width: 35px;
			aspect-ratio: 1 / 1;
			object-fit: contain;
			object-position: center;
		}
	}

	&__title {
		font-size: 24px;
		line-height: 1.2;
		color: $accent-color;
		font-weight: 500;
	}
}

.btn {
	border-radius: 10px;
	padding: 12px 16px 16px;
	transition:
		background-color 400ms,
		color 400ms,
		outline 300ms ease-out;

	&_social {
		cursor: pointer;
		outline: 1px solid transparent;
		transition: outline 300ms ease-out, scale 300ms ease-out;
	}

	&_social:hover {
		outline: 1px solid $accent-color;
		scale: 1.015;
	}

	&_accent {
		background-color: $accent-color;
		color: $color-white;
		outline: 1px solid transparent;

		&:hover {
			background-color: $light-color;
			outline: 1px solid $accent-color;
			color: $accent-color;
		}
	}

	&_light {
		background-color: $color-white;
		color: $accent-color;
		outline: 2px solid transparent;

		&:hover {
			background-color: $light-color;
			outline: 1px solid $accent-color;
			color: $accent-color;
		}
	}
}


.point-mark {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 50px;
	aspect-ratio: 1/1;
	background-color: $light-color;
	border-radius: 10px;
	position: relative;
}

.point-map::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/point-map.svg);
}

.point-tel::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/tel.svg);
}

.point-tg::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/tg.svg);
}

.point-wa::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/wa.svg);
}

.point-plus::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/plus.svg)
}

.point-menu::before {
	@include point-mark-psevdo;
	background-image: url(/assets/img/icons/cuboid.svg)
}

.social-container {
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 10px;
	font-size: clamp(1.25rem, 1.0634rem + 0.597vw, 1.5rem);
	font-weight: 600;
	line-height: 1.2;

	&__point {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: clamp(0.375rem, -0.0904rem + 1.0638vw, 0.6875rem);
		background-color: $color-white;
		border-radius: 50%;
	}

	svg {
		width: clamp(1.125rem, 0.6596rem + 1.0638vw, 1.4375rem);
	}
}

.social-tg {
	padding:
		clamp(0.5rem, 0.0346rem + 1.0638vw, 0.8125rem) clamp(0.5rem, 0.0346rem + 1.0638vw, 0.8125rem) clamp(0.4375rem, -0.0279rem + 1.0638vw, 0.75rem) clamp(0.25rem, -0.2154rem + 1.0638vw, 0.5625rem);
}

@import 'vendor/footer';
@import 'vendor/intro.scss';